import * as React from "react"
import Layout from "../components/layout"
import RaceAreaLink from "../components/race_area_link"
import { graphql } from "gatsby"

export default function Forecasts({data}) {
  const day = data.region.edges[0].node.date

  function formatDate(dt) {
    dt = new Date(dt)
    var y = dt.getFullYear();
    var m = ('00' + (dt.getMonth() + 1)).slice(-2);
    var d = ('00' + dt.getDate()).slice(-2);
    return (y + '-' + m + '-' + d);
  }

  const today = formatDate(new Date());

  function view_link(edge){
    if (today === edge.node.date){
      return <RaceAreaLink key={edge.node.race_id} post={edge.node} />
    }
  }

  return (
    <Layout>
      <h2>{day} 地方競馬</h2>
        {data.region.edges.map(edge => view_link(edge))}
    </Layout>
  )
}

export const query = graphql`
  query race_info {
      region: allTodayRaceInfoCsv{
        edges {
          node {
            division
            date
            RaceData01_time
            area
            race_id
          }
        }
      }
    }
`